import React from 'react';
import PropTypes from 'prop-types';
import { SectionProps } from '../../utils/SectionProps';
import SmartContractLoader from '../elements/SmartContractLoader';
import { useTranslation } from "react-i18next";
import classNames from 'classnames';


const propTypes = {
  children: PropTypes.node,
  ...SectionProps.types
}

const defaultProps = {
  children: null,
  ...SectionProps.defaults
}

const Stats= ({
  className,
  children,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {
  const outerClasses = classNames(
    " section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
  );

  const innerClasses = classNames(
    "section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );
  const [t] = useTranslation("common");
  const sectionHeader= classNames(
    "center-content"
    );
  return (
          <div className={outerClasses}>

      <div className={innerClasses}>
      <div className="stats-container">
      <h1 className={sectionHeader}>{t("Stats.section.title")}</h1>
    <SmartContractLoader t={t}/>
    </div>
    </div>
        </div>
    );
}

Stats.propTypes = propTypes;
Stats.defaultProps = defaultProps;

export default Stats;